import { autoWebViewJs } from "@droid/android";
import { Box, Button } from "@mui/material";


export function TestEmergencyCall() {
    function onClick(){
        const script = `
        var instance = com.fanfanlo.emergencycall.service.SensorMonitoringService.instance;
        if(instance){
            instance.triggerEmergencyCall();
        }
        `
        autoWebViewJs.callScript(script);
    }
    return(
        <Box sx={{mt:"10px", mb:"10px"
        }}>
            <Button onClick={onClick}>
                
            </Button>
        </Box>  
    )
}