import React, { useState, useEffect, useCallback } from 'react';
import {
    Box,
    Switch,
    FormControlLabel,
    TextField,
    Button,
    List,
    Typography,
    Paper,
    Select,
    MenuItem,
    FormControl,
    InputLabel,
    Chip,
    Stack,
    IconButton
} from '@mui/material';
import {
    DndContext,
    closestCenter,
    KeyboardSensor,
    PointerSensor,
    useSensor,
    useSensors,
    DragEndEvent,
} from '@dnd-kit/core';
import {
    arrayMove,
    SortableContext,
    sortableKeyboardCoordinates,
    useSortable,
    verticalListSortingStrategy,
} from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import { Delete as DeleteIcon, Add as AddIcon, DragIndicator as DragIndicatorIcon, PlayCircleOutline as PlayIcon } from '@mui/icons-material';
import { debounce } from 'lodash';
import { autoJsStoreUtils, autoWebViewJs } from '@droid/android';

const PRE_ALARM_CONFIG_KEY = 'PRE_ALARM_ACTIONS_CONFIG';

// --- Data Structures ---
interface PreAlarmAction {
    id: string;
    type: "sound" | "vibration";
    duration: number;
    resourceId?: string;
}

interface PreAlarmConfig {
    enabled: boolean;
    actions: PreAlarmAction[];
    maxWaitDuration: number;
}

const defaultSoundResources = [
    { id: 'beep_short', name: '' },
    { id: 'siren_gentle', name: '' },
    { id: 'alarm_clock', name: '' },
];

// --- Sortable Item Component ---
const SortableItem: React.FC<{
    action: PreAlarmAction;
    onUpdate: (id: string, updates: Partial<Omit<PreAlarmAction, 'id'>>) => void;
    onRemove: (id: string) => void;
}> = ({ action, onUpdate, onRemove }) => {
    const {
        attributes,
        listeners,
        setNodeRef,
        transform,
        transition,
        isDragging,
    } = useSortable({ id: action.id });

    const style = {
        transform: CSS.Transform.toString(transform),
        transition,
        zIndex: isDragging ? 1 : 'auto',
        boxShadow: isDragging ? '0px 4px 8px rgba(0,0,0,0.2)' : 'none',
    };
    function handlePreviewSound(id:string){
        const script = `
        com.fanfanlo.emergencycall.manager.SoundManager.previewSound("${id}");
        `;
        autoWebViewJs.callScript(script);
    }
    return (
        <Paper
            ref={setNodeRef}
            style={style}
            elevation={1}
            sx={{
                width: '100%',
                p: 1.5,
                mb: 1.5,
                border: '1px solid #ddd',
                borderRadius: '8px',
                bgcolor: 'background.paper',
                display: 'flex',
                gap: 1,
                userSelect: 'none',
            }}
        >
            <Box {...attributes} {...listeners} sx={{ display: 'flex', alignItems: 'center', cursor: 'grab', alignSelf: 'flex-start', pt: 0.5, touchAction: 'none' }}>
                <DragIndicatorIcon />
            </Box>
            <Stack spacing={1.5} sx={{ flexGrow: 1 }}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    <Chip label={action.type === 'sound' ? '' : ''} color={action.type === 'sound' ? 'primary' : 'secondary'} size="small" />
                    <IconButton size="small" aria-label="delete" onClick={() => onRemove(action.id)}>
                        <DeleteIcon fontSize="small" />
                    </IconButton>
                </Box>
                <TextField
                    label=" (ms)"
                    type="number"
                    size="small"
                    value={action.duration}
                    onChange={(e) => onUpdate(action.id, { duration: parseInt(e.target.value, 10) || 0 })}
                    fullWidth
                />
                {action.type === 'sound' && (
                    <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
                        <FormControl size="small" fullWidth>
                            <InputLabel></InputLabel>
                            <Select
                                value={action.resourceId}
                                label=""
                                onChange={(e) => onUpdate(action.id, { resourceId: e.target.value })}
                            >
                                {defaultSoundResources.map(res => (
                                    <MenuItem key={res.id} value={res.id}>{res.name}</MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                        <IconButton 
                            aria-label="preview sound" 
                            onClick={() => handlePreviewSound(action.resourceId)}
                            size="small"
                        >
                            <PlayIcon />
                        </IconButton>
                    </Box>
                )}
            </Stack>
        </Paper>
    );
};


// --- Main Component ---
const PreAlarmConfigComponent: React.FC = () => {
    const [config, setConfig] = useState<PreAlarmConfig | null>(null);

    const debouncedWrite = useCallback(debounce((newConfig: PreAlarmConfig) => {
        autoJsStoreUtils.write(PRE_ALARM_CONFIG_KEY, newConfig);
    }, 500), []);

    useEffect(() => {
        const fetchConfig = async () => {
            const storedConfig = await autoJsStoreUtils.read<PreAlarmConfig>(PRE_ALARM_CONFIG_KEY);
            if (storedConfig && typeof storedConfig === 'object') {
                const actionsWithIds = storedConfig.actions.map(a => ({ ...a, id: a.id || Math.random().toString(36).substr(2, 9) }));
                setConfig({ ...storedConfig, actions: actionsWithIds });
            } else {
                setConfig({
                    enabled: true,
                    maxWaitDuration: 30000,
                    actions: [],
                });
            }
        };
        fetchConfig();
    }, []);

    const handleConfigChange = (newConfig: Partial<PreAlarmConfig>) => {
        setConfig(prev => {
            if (!prev) return null;
            const updatedConfig = { ...prev, ...newConfig };
            debouncedWrite(updatedConfig);
            return updatedConfig;
        });
    };

    const handleAddAction = (type: 'sound' | 'vibration') => {
        const newAction: PreAlarmAction = {
            id: `action-${Date.now()}`, // More robust ID
            type,
            duration: 3000,
            ...(type === 'sound' && { resourceId: defaultSoundResources[0].id }),
        };
        handleConfigChange({ actions: [...(config?.actions || []), newAction] });
    };

    const handleRemoveAction = (id: string) => {
        handleConfigChange({ actions: config?.actions.filter(a => a.id !== id) });
    };

    const handleUpdateAction = (id: string, updates: Partial<Omit<PreAlarmAction, 'id'>>) => {
        handleConfigChange({
            actions: config?.actions.map(a => (a.id === id ? { ...a, ...updates } : a)),
        });
    };

    const sensors = useSensors(
        useSensor(PointerSensor),
        useSensor(KeyboardSensor, {
            coordinateGetter: sortableKeyboardCoordinates,
        })
    );

    const handleDragEnd = (event: DragEndEvent) => {
        const { active, over } = event;
        if (over && active.id !== over.id) {
            setConfig((prev) => {
                if (!prev) return null;
                const oldIndex = prev.actions.findIndex(item => item.id === active.id);
                const newIndex = prev.actions.findIndex(item => item.id === over.id);
                const newActions = arrayMove(prev.actions, oldIndex, newIndex);
                
                // We call handleConfigChange to trigger the debounced write
                handleConfigChange({ actions: newActions });

                // Return the new state directly for immediate UI update
                return { ...prev, actions: newActions };
            });
        }
    };

    if (!config) {
        return <Typography>Loading configuration...</Typography>;
    }

    return (
        <Box sx={{ p: 1, margin: 'auto' }}>
            <Paper elevation={2} sx={{ p: 2 }}>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>

                <FormControlLabel
                    control={<Switch checked={config.enabled} onChange={e => handleConfigChange({ enabled: e.target.checked })} />}
                    label=""
                />

                <TextField
                    label=" ()"
                    type="number"
                    value={config.maxWaitDuration}
                    onChange={e => handleConfigChange({ maxWaitDuration: parseInt(e.target.value, 10) || 0 })}
                    fullWidth
                    margin="normal"
                />

                <Typography variant="subtitle1" sx={{ mt: 2, mb: 1 }}>
                    
                </Typography>

                <DndContext
                    sensors={sensors}
                    collisionDetection={closestCenter}
                    onDragEnd={handleDragEnd}
                >
                    <SortableContext
                        items={config.actions.map(a => a.id)}
                        strategy={verticalListSortingStrategy}
                    >
                        <List disablePadding>
                            {config.actions.map(action => (
                                <SortableItem 
                                    key={action.id} 
                                    action={action} 
                                    onUpdate={handleUpdateAction} 
                                    onRemove={handleRemoveAction} 
                                />
                            ))}
                        </List>
                    </SortableContext>
                </DndContext>

                <Stack direction="row" spacing={1} sx={{ mt: 1 }}>
                    <Button startIcon={<AddIcon />} onClick={() => handleAddAction('vibration')} variant="outlined" size="small">
                        
                    </Button>
                    <Button startIcon={<AddIcon />} onClick={() => handleAddAction('sound')} variant="outlined" size="small">
                        
                    </Button>
                </Stack>
            </Paper>
        </Box>
    );
};

export default PreAlarmConfigComponent;