import { IRequestParams, IResultContent, request } from "@fanfanlo";
import { tms } from "../tms";
import { IRequestOptions } from "../IRequestOptions";

/**
 * @description 
 */
export interface IEmcContactIdentity {
    identity: string;
    identityType: 'email' | 'mobile';
}

/**
 * @description 
 */
export interface IEmcInviteContactsParams extends IRequestParams {
    contacts: IEmcContactIdentity[];
    remark?: string;
    type: string;
    message?: string;
    matrix_access_token: string; //  Matrix  Token
}

/**
 * @description 
 */
export interface IEmcInviteResultItem {
    identity: string;
    result_code: string; //  ResultCode 
    msg: string;
}

/**
 * @description 
 */
