export interface IEmcInviteContactsResult extends IResultContent {
    data: {
        is_ok:boolean;
        results: IEmcInviteResultItem[];
        msg?: string;
    };
}

/**
 * @description 
 * @param opts  params, gateway, handler 
 */
export async function tmsEmcInviteContacts(opts: IRequestOptions<IEmcInviteContactsParams>) {
    const res = await request<IEmcInviteContactsResult>(tms.TEmc, tms.MEmcInviteContacts, opts.params, opts.gateway, opts.handler);
    return res;
}