import { autoWebViewJs } from "@droid/android";
import { Box, Button } from "@mui/material";
import { useEffect, useState } from "react";


export function JpushIsInitialized(){
    const [isInitialized, setIsInitialized] = useState(false);
    function refresh(){
        const script = `var bl = com.fanfanlo.emergencycall.jpush.JpushManager.readIsJpushInitialized();
        bl;`
        const {javaResultData} = autoWebViewJs.callScript<boolean>(script);
        setIsInitialized(typeof javaResultData == "undefined" ? false : javaResultData);
    }
    useEffect(() => {
        refresh();
    }, []);

    return(
        <Box>
            <span>:</span><span>{isInitialized ? "" : ""}</span>
            <span><Button onClick={refresh}></Button></span>
        </Box>
    )
}